import React, { useState } from "react";
import {
  IonPopover,
  IonContent,
  IonItem,
  IonLabel,
  IonButton,
  IonFab,
  IonFabButton,
  IonIcon,
  IonRow,
  IonCol,
  IonSelect,
  IonSelectOption,
} from "@ionic/react";
import { add, settingsOutline } from "ionicons/icons";
import styled from "styled-components";
import { ColorDet, SoundDet } from "../../pages/Home";
import "./index.scss";

declare global {
  namespace JSX {
    interface IntrinsicElements {
      "ion-list": React.DetailedHTMLProps<
        React.HTMLAttributes<HTMLElement>,
        HTMLElement
      >;
    }
  }
}

interface Props {
  onClickProp: any;
}

const Themes = styled.div`
  // display: flex;
  // align-items: center;
  // justify-content: center;
  // gap: 25px;
`;

export const PopOver: React.FC<Props> = (props) => {
  let [colors] = useState([
    { name: "default", code: "#da3768" },
    { name: "color-1", code: "#055CDF" },
    { name: "color-2", code: "#09EB8D" },
    { name: "color-3", code: "#03D3F8" },
  ]);

  let [sounds] = useState([
    {
      name: "sound1",
      url: "https://www.soundhelix.com/examples/mp3/SoundHelix-Song-1.mp3",
    },
    {
      name: "sound2",
      url: "https://www.soundhelix.com/examples/mp3/SoundHelix-Song-12.mp3",
    },
    {
      name: "sound3",
      url: "https://www.soundhelix.com/examples/mp3/SoundHelix-Song-14.mp3",
    },
  ]);
  const [defaultCol, setDefaultCol] = React.useContext(ColorDet);
  const [defaultSound, setSound] = React.useContext(SoundDet);
  const [active, setActive] = useState("default");
  const [soundName, setSoundName] = useState("");
  return (
    <>
      {props.children}
      {/* Make popover show relative to click coordinates rather than trigger */}
      {/* <IonButton id="size-button">Click to open popover</IonButton>
      <IonPopover trigger="size-button" reference="event">
        <IonContent>Popover Content</IonContent>
      </IonPopover> */}

      {/* Nested Popover */}
      {/* <IonButton id="nested-button">Click to open popover</IonButton> */}
      <IonFab id="settings-btn" vertical="top" horizontal="end" slot="fixed">
        <IonFabButton size="small" id="nested-button">
          <IonIcon icon={settingsOutline} />
        </IonFabButton>
      </IonFab>
      <IonPopover id="popover-btn" side="bottom" trigger="nested-button" dismissOnSelect={true}>
        <IonContent class="ion-padding">
          <IonRow>
            <h5>Themes</h5>
          </IonRow>
          <IonRow class="ion-align-items-center ion-justify-content-center">
            {colors.map((color: any, i: number) => (
              <IonCol size="3">
                <div
                  className={`${color.name} commCircle colordiv ${
                    active == color.name ? `active` : ``
                  } cursor-pointer`}
                  id={`colorid-${i + 1}`}
                  onClick={() => {
                    setActive(color.name);
                    setDefaultCol(color.code);
                  }}
                ></div>
              </IonCol>
            ))}
          </IonRow>
          <IonRow>
            <h5>Sounds</h5>
          </IonRow>
          <IonRow class="ion-align-items-center ion-justify-content-center">
            <IonCol size="12">
              <IonSelect
                id="soundSelect"
                value={soundName}
                placeholder="Choose Sound"
                onIonChange={(e: any) => {
                  let url: any = sounds.find(x => x.name == e.detail.value)?.url;
                  setSound(url);
                  setSoundName(e.detail.value);
                }}
              >
                {sounds.map((sound, i) => (
                  <IonSelectOption id={`sound-${i}`} value={sound.name}>{sound.name}</IonSelectOption>
                ))}
              </IonSelect>
            </IonCol>
          </IonRow>
        </IonContent>
      </IonPopover>
    </>
  );
};
